@media (max-width: 1440px) {
  .hero {
    &__title {
      font-size: 136px;
      line-height: 150px;
    }
  }
}

@media (max-width: 1024px) {
  .hero {
    &__title {
      font-size: 100px;
      line-height: 120px;
    }
  }
}

@media (max-width: 768px) {
  .hero {
    &__title {
      font-size: 66px;
      line-height: 110px;
    }
  }
}

@media (max-width: 540px) {
  .hero {
    &__title {
      font-size: 40px;
      line-height: 56px;
    }
  }
}

